	module.exports = {"data":{"markdownRemark":{"html":"<h2>Women In Tech</h2>\n<p>From an article on <a href=\"https://www.themuse.com/advice/the-latest-stats-on-women-in-tech\">“The Latest Stats on Women in Tech”</a>,</p>\n<blockquote>\n<p>“We all know that there’s a major gender gap when it comes to women in tech. </p>\n<p>Although we make up the majority of the workplace and the social media sphere, we only represent a tiny percentage of the technology world.</p>\n<p>And that’s reflected in some of the stats you see. For example, according to a 2011 report from the Level Playing Field Institute, ‘82% of men in startups believed their companies spent the ‘right amount of time’ addressing diversity, while almost 40% of women believed not enough time was devoted”</p>\n</blockquote>\n<p><img src=\"https://www.womenwhotech.com/wp-content/uploads/infographic_700_FINAL.png\" alt=\"Alt text\" title=\"Women in Tech\"></p>\n<p>Although women are still underrepresented in the tech world it does not mean that we are not making a difference in the tech world.</p>\n<p>In a Women in Tech Round-up event presented by Google in Austin, the Women who spoke at these events were vice presidents (VPs) and high-level management positions from profitable companies working in the tech industry. Not all of them began their careers in the tech world many had started as secretaries and other roles that are often geared towards women. They worked hard and took risks to get where they are now. The one piece of advice that seemed to resound from all of them was that for you to succeed you must take risks. </p>\n<p>The Google VP who spoke at this event gave us all a piece of advice: that we should take any and every opportunity afford to us and risk  applying to jobs we are not qualified for. The worst that will happen is you’ll get rejected or you won’t get an interview. Men get tech positions not because they are more experienced or better than women but because they apply to positions that they often aren’t qualified for while women only apply for jobs that they think they are qualified for.\nThe worst thing you can do is to sell yourself short if you want something take that risk and achieve the impossible.</p>\n<h3>More Information:</h3>\n<ul>\n<li><a href=\"https://www.theguardian.com/lifeandstyle/2017/aug/08/why-are-there-so-few-women-in-tech-the-truth-behind-the-google-memo\">Why are there so few women in tech? The truth behind the Google memo</a></li>\n<li><a href=\"https://www.nytimes.com/2017/09/16/opinion/sunday/ellen-pao-sexism-tech.html\">Ellen Pao: Has Anything Really Changed for Women in Tech?</a></li>\n</ul>","fields":{"slug":"/working-in-tech/women-in-tech/"},"frontmatter":{"title":"Women in Tech"}}},"pathContext":{"slug":"/working-in-tech/women-in-tech/"}}

